<script>
import {validatePhone} from "@/utils/reg" //引入号码验证
export default {
  data () {
    return {
      phoneNumber:'',
      password:''
    }
  },
  computed:{
    visible(){
      return this.$store.state.login.visible
    }
  },
  methods:{
    //
    login(){
      
      this.axios.post('/api/login',{
        username:this.phoneNumber,
        password:this.password
      }).then((res)=>{
        console.log(res);
      }).catch(err=>{
        console.log(err);
      })
      // if(!validatePhone(this.phoneNumber)){
      //   this.$message.error({message:'手机号码验证失败',center: true});
      //     return false  // 如果不写false ,默认undefined
      //   }
      //   if (this.password == '') {
      //     this.$message.error({message:'请输入正确的密码',center: true});
      //     return false
      //   }
      //   this.$message({
      //     message: '登录成功',
      //     type: 'success',
      //     center:true
      //   });
      this.$store.commit('login/show',false)
      this.$store.commit('login/isShow',false)
    },
    isShow(){
      if (this.$refs.op.type=='password') {
        this.$refs.op.type = 'text'
      } else {
        this.$refs.op.type = 'password'
      }
      
    }
  }
}
</script>
 
