<template>
  <div class="top">
    <div class="wrap">
 <!-- 顶部 -->
   <div class="hand">
    <div class="l">
      <span style="-webkit-text-stroke: 1px yellow;letter-spacing:5px">松鼠乐园欢迎你</span>
      <div class="search">
        <el-input placeholder="请输入内容" class="input" v-model="input" clearable></el-input> 
      </div>
      <i class="el-icon-search"></i>
    </div>
    <div class="r">
      <el-button :plain="true" @click="login" v-if="isShow">登录</el-button>
      <!-- <span @click="login" v-if="isShow">登录</span>
      <span v-if="!isShow" @click="close">退出登录</span> -->
      <el-button :plain="true" @click="close" v-if="!isShow">退出登录</el-button>
    </div>
   </div>
   <!-- 轮播图 -->
   <div class="img">

    </div>
   
   </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      input: ''
    }
  },
  computed:{
    isShow(){
     return this.$store.state.login.isShow
    }
  },
  methods:{
    login(){
      this.$store.commit("login/show",true)
    },
    close(){
      this.$store.commit("login/isShow",true)
      this.$message({
          message: '退出登录成功',
          type: 'success',
          center:true
        });
    }
  }
}
</script>
 
<style lang = "less" scoped>
  .top{
    width: 100%;
    background-color: #242b39;
    .hand{
      display: flex;
      height: 50px;
      line-height: 50px;
      justify-content: space-between;
      width: 100%;
      .l{
        display: flex;
        font-weight: 700;
        .search{
          .input{
            margin-left: 30px;
            height: 20px;
          }
          
        }
        i{
            margin-left: 50px;
            margin-top: 10px;
            font-size: 30px;
            color: #fff;
          }
      }
      .r{
        span{
          margin: 10px 20px;
          padding:5px 15px;
          color: #fff;
          cursor: pointer;
        }
        /deep/ .el-button{
          background-color: #242b39;
