<template>
  <div class="swiper-container wrap img" ref='Carousel'>
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for="Carousel in list" :key="Carousel.id">
        <img :src="Carousel.imgUrl" />
      </div>
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination"></div>

    <!-- 如果需要导航按钮 -->
    <div class="swiper-button-prev"></div>
    <div class="swiper-button-next"></div>
  </div>
</template>

<script>
// 引入swiper的js
import Swiper from "swiper/swiper-bundle.js";
import "swiper/swiper-bundle.min.css";

export default {
  name: "Carousel",
  // props: ["list"],
  data(){
    return{
      list:[
      {
        id:1,
        imgUrl:'https://img1.baidu.com/it/u=3418967672,350439900&fm=253&fmt=auto&app=138&f=JPEG?w=889&h=500'
      },
      {
        id:2,
        imgUrl:'https://img1.baidu.com/it/u=99651604,1098251864&fm=253&fmt=auto&app=120&f=JPEG?w=1422&h=800'
      },
      {
        id:3,
        imgUrl:'https://img0.baidu.com/it/u=981632608,1425917516&fm=253&fmt=auto?w=800&h=500'
      },
      {
        id:4,
        imgUrl:'https://img-baofun.zhhainiao.com/fs/9b544155efe3d82f1a509d09e6df5bfc.jpg'
      },
    ]
    }
  },
  watch: {
    list: {
      immediate: true,
      handler() {
        this.$nextTick(() => {
          let mySwiper = new Swiper(this.$refs.Carousel, {
            direction: "horizontal", // 垂直切换选项
            loop: true, // 循环模式选项
            autoplay: {
　　　　       delay: 2000,
　　           disableOnInteraction: false
　　       },
            // 如果需要分页器
            pagination: {
              el: ".swiper-pagination",
              clickable: true,
              type: "bullets",
            },

            // 如果需要前进后退按钮
            navigation: {
              nextEl: ".swiper-button-next",
              prevEl: ".swiper-button-prev",
            },
          });
        });
      },
    },
  },
};
</script>

<style lang="less" scoped>
.swiper-slide{
  img{
    width: 1200px;
    height: 446px;
  }
}
.img{
  margin: 20px auto;
}
</style>